
import React, { useState, useEffect } from 'react'
import hint from './img/hint.svg'
import skip from './img/skip.svg'
import axios from 'axios'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import { setCookie, getCookie } from '../../utils/utils'
import apiProxy from '../../model/ApiProxy'
import './hint.less'
import { DeriEnv } from '../../lib/web3js/shared/config/env';
export default function Announcement({ wallet,isPower }) {
  const [show, setShow] = useState(false)
  const [isSkip, setIsSkip] = useState(true)
  const [symbols, setSymbols] = useState([])
  const [isVip, setIsVip] = useState(true)
  const closeHint = () => {
    sessionStorage.setItem('trade-power-hint', "false",1)
    setShow(false)
  }
  const getRetiredSymbolsInfo = async () => {
    let res = await apiProxy.request('getRetiredSymbolsInfo', ['56',wallet.detail.account])
    if (res.length) {
      setSymbols(res)
    }

  }

  useEffect(() => {
    if(symbols.length){
      let sess = sessionStorage.getItem('trade-power-hint')
      if(sess === "false"){
        return;
      }
      if (symbols[0].volume !== "0" ||  symbols[1].volume !== "0") {
        setShow(true)
      }
    }
    

  }, [symbols])

  useEffect(() => {
    if (wallet.isConnected() && +wallet.detail.chainId === 56) {
      getRetiredSymbolsInfo()
    }
  }, [wallet, wallet.detail, wallet.detail.account])
  return (
    DeriEnv.get() === 'prod' ? <div>
      {show && isPower && <div className={isVip ? 'trade-hint  close-position-hint have-vip' : 'trade-hint  close-position-hint'}>
        <div className='hint-img'>
          <img src={hint} alt='' />
        </div>
        <div className='hint-box'>
          <div className='hint-title'>
            <span>
              For Your Information
            </span>
            <img className='close' src={closeModalIcon} alt='X' onClick={closeHint} />
          </div>
          <div className='hint-content'>
            <div className='hint-text vip-text'>
              mBTC^2 and mETH^2 have been migrated to the Main Pool
              <br />
              <br />
              Please be suggested to close positions in the <a target='_blank' rel='noreferrer' href="https://deri.io/#/retired/trade/powers">retired market</a>, and reopen the position in this page after
            </div>
            <div className='hint-link vip-skip'>
              <div className={isSkip ? 'skip check-skip' : 'skip'} onClick={() => setIsSkip(!isSkip)}>
                {isSkip && <img src={skip} />}
              </div>  <span>skip notification until tomorrow</span>
            </div>
          </div>
        </div>
        <div className='m-check' onClick={closeHint}>
          <span className='check'>
            OK
          </span>
        </div>
      </div>}

    </div> : null

  )
}